.header {
  display: flex;
  width: 100%;
  min-height: 88px;
  box-sizing: border-box;
  background-color: #1c1c21;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  display: flex;
  width: 1280px;
  justify-content: space-between;
  align-items: center;
}

.nav {
  display: flex;
  margin-right: -175px;
}

.navLink {
  display: flex;
  text-decoration: none;
  color: #F2F2F3;
}


